import React from 'react';
import PropTypes from 'prop-types';

// import { connect } from 'react-redux';

class Footer extends React.Component {
    render() {
        return <React.Fragment>
            <div className="FooterPlaceholder" style={{height: '1em'}}></div>
            <div style={{ position: 'absolute', bottom: 0, left: 0, right: 0, background: 'black', color: 'white', fontSize: '0.75em' }}>
                <div style={{ width: '33%', display: 'inline-block', textAlign: 'left' }}>
                    Copyright &copy; 2016 {this.props.displayName}. {/* All Rights Reserved. */}
                </div>
                <div style={{ width: '34%', display: 'inline-block', textAlign: 'center' }}>
                    <a style={{ color: 'white' }}
                        href={'mailto:' + this.props.emailAddress + '?subject=' + this.props.emailSubject}
                    >
                        {this.props.emailAddress}
                    </a>
                </div>
                <div style={{ width: '33%', display: 'inline-block', textAlign: 'right' }}>
                    <a style={{ color: 'white' }}
                        href={'https://' + this.props.homepageAddress}
                    >
                        {this.props.homepageAddress}
                    </a>
                </div>
            </div>
        </React.Fragment>
    }
}

Footer.propTypes = {
    displayName: PropTypes.string,
    emailAddress: PropTypes.string,
    emailSubject: PropTypes.string,
    homepageAddress: PropTypes.string,
}

